input {
  &:-ms-input-placeholder,
  &:-moz-placeholder,
  &::-moz-placeholder,
  &::-webkit-input-placeholder {
    color: #ccc;
  }
}

.txt,
select {
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 1rem;
  transition: border 0.1s linear;
  &:focus,
  &:active {
    outline: none;
  }
}

select {
  border: 1px solid #ccc;
  &:focus,
  &:active {
    border-color: #333;
  }
}

.txt {
  border: 1px solid transparent;
  position: relative;
  border-bottom: 1px solid #ccc;
}

.isNecessary {
  position: relative;
  &:after {
    content: "*";
    display: inline-block;
    color: #ccc;
    height: 100%;
    transition: all 0.2s linear;
  }
}

.search-panel {
  margin-top: 10px;
}

.btn-panel {
  margin-top: 10px;
  .btn {
    margin: 5px 0;
  }
}

.table {
  margin-bottom: 20px;
  table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    border: 1px solid #ccc;
  }
  caption {
    margin-bottom: 10px;
  }
  th {
    padding: 0.5rem;
  }
  td {
    padding: 0.3rem;
  }
  th,
  td {
    border: 1px solid #aaa;
  }
  thead {
    tr {
      &:nth-child(2n-1) {
        background-color: #2a85c2;
        color: #fff;
      }
    }
  }
  tbody {
    tr {
      &:nth-child(2n) {
        background-color: #efefef;
      }
    }
  }
}

.table-mobile {
  margin: 15px -10px 20px;
  h3 {
    margin: 10px 10px 5px;
    padding-bottom: 10px;
    border-bottom: 2px solid #2e6da4;
  }
  li {
    padding: 5px 10px;
    border-bottom: 1px solid #1778d0;
    background-color: #fff;
    display: flex;
    justify-content: flex-start;
    flex-flow: row wrap;
    align-items: center;
    &:nth-child(2n) {
      background-color: rgba(92, 184, 92, 0.2);
    }
    &:last-child {
      border-bottom: 1px solid transparent;
    }
    &>div {
      padding: 3px;
      &:nth-child(1) {
        width: 7rem;
        font-size: 1.2rem;
        font-weight: bold;
        color: #333;
      }
      &:nth-child(2) {
        width: calc(100% - 7rem);
        font-size: 0.9rem;
      }
      &:nth-child(3) {
        width: 100%;
        font-size: 0.7rem;
      }
      &:nth-child(4) {
        width: 100%;
        font-size: 0.7rem;
        color: #999;
      }
      span {
        &:nth-child(1) {
          font-weight: bold;
        }
      } // border-bottom: 1px solid #ccc;
      // &:last-child {
      //   border-bottom: 1px solid transparent;
      // }
    } // span {
    // font-size: 1.5ch;
    // width: 5.5em;
    // &+span {
    //   width: 90%;
    //   padding-left: 1rem;
    // }
    // }
  }
}

.justify {
  text-align: justify;
  text-align-last: justify;
  display: inline-block;
  width: 100%;
  position: relative;
  &:after {
    content: ":";
    position: absolute;
    display: inline-block;
    right: -5px;
  }
}

/* 文本框及基础 */

.label {
  height: 4rem;
  padding-top: 2rem;
  position: relative;
  z-index: 1;
  &:after {
    content: "";
    position: absolute;
    width: 0;
    height: 2px;
    bottom: 0;
    left: 50%;
    background-color: #2b669a;
    display: block;
    transition: all 0.1s linear;
    z-index: 5;
  }
  &:before {
    content: attr(errormsg);
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    background-color: #fff;
    display: block;
    color: #f00;
    z-index: 5;
  }
  &.has-select {
    &>span {
      top: 5px;
    }
  }
  &.has-mult {
    &>span {
      top: 5px;
    }
    height: auto;
  }
  &>span {
    color: #999;
    position: absolute;
    width: 100%;
    height: 2rem;
    top: 2rem;
    left: 0;
    z-index: 1;
    transition: all 0.1s linear;
  }
  .txt {
    position: relative;
    background-color: transparent;
    z-index: 3;
  }
  &.focus {
    .isNecessary {
      &:after {
        color: #f00;
      }
    }
    &>span {
      top: 5px;
      color: #2b669a;
      font-weight: bold;
    }
    &:after {
      left: 0;
      width: 100%;
    }
  }
}

/* 按钮 */

.btn {
  --left: 0;
  --top: 0;
  cursor: pointer;
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 1rem;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  &:after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    top: var(--top);
    left: 0;
    left: var(--left);
    pointer-events: none;
    background-image: radial-gradient( circle, rgba(0, 0, 0, 1) 10%, transparent 10.01%);
    background-repeat: no-repeat;
    background-position: 50%;
    transform: scale(10, 10);
    opacity: 0;
    transition: transform 0.5s ease, opacity 0.5s ease;
  }
  &:active {
    &:after {
      transform: scale(0, 0);
      opacity: 0.2;
      transition: 0s;
    }
  }
}

.btn-mid {
  padding: 12px 16px;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-default {
  color: #333;
  background-color: #fff;
  border-color: #ccc;
}

.btn-primary {
  color: #fff;
  background-color: #337ab7;
  border-color: #2e6da4;
}

.btn-success {
  color: #fff;
  background-color: #5cb85c;
  border-color: #4cae4c;
}

.btn-info {
  color: #fff;
  background-color: #5bc0de;
  border-color: #46b8da;
}

.btn-waring {
  color: #fff;
  background-color: #ff9800;
  border-color: #f09711;
}

.btn-danger {
  color: #fff;
  background-color: #d9534f;
  border-color: #d43f3a;
}

/* radio */

.radio {
  display: block;
  padding: 5px;
  input {
    display: none;
    &:checked {
      &+label {
        &:after {
          background-color: #4cae4c;
          border: 2px solid #4cae4c;
          transform: scale(1.02);
        }
        &:before {
          border: 2px solid transparent;
        }
      }
    }
  }
  label {
    display: inline-block;
    vertical-align: middle;
    font-size: 1.2rem;
    transition: all 0.2s ease;
    position: relative;
    padding-left: 2rem;
    cursor: pointer;
    user-select: none;
    width: 100%;
    &:after,
    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      z-index: 0;
      margin: auto;
      display: inline-block;
      vertical-align: middle;
      width: 1.2rem;
      height: 1.2rem;
      background-color: #fff;
      border: 2px solid #5a5a5a;
      border-radius: 100%;
      transform: scale(1.02);
      transition: all 0.2s ease;
    }
    &:after {
      transform: scale(0);
    }
  }
}

/* checkbox */

.checkbox {
  display: block;
  padding: 5px;
  input {
    display: none;
    &:checked {
      &+label {
        &:before {
          top: 0;
          left: -0.1rem;
          width: 0.6rem;
          height: 1.2rem;
          border-top: 2px solid transparent;
          border-left: 2px solid transparent;
          border-right: 2px solid #26a69a;
          border-bottom: 2px solid #26a69a;
          transform: rotate(40deg);
          backface-visibility: hidden;
          transform-origin: 100% 100%;
        }
      }
    }
  }
  label {
    display: inline-block;
    vertical-align: middle;
    font-size: 1.2rem;
    position: relative;
    padding-left: 2rem;
    cursor: pointer;
    user-select: none;
    width: 100%;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      margin: auto;
      width: 1.2rem;
      height: 1.2rem;
      z-index: 0;
      border: 2px solid #5a5a5a;
      border-radius: 1px;
      transition: all 0.2s ease;
    }
    &:after {
      border: 0;
      transform: scale(0);
    }
  }
}

/* select */

.md-select {
  position: relative;
  z-index: 10;
  span {
    top: 5px;
  }
  .select {
    position: absolute;
    left: 0;
    top: 2rem;
    z-index: 20;
    background-color: #fff;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
    width: 0;
    height: 0;
    overflow: hidden;
    transition: all 0.2s ease;
    opacity: 0;
  }
  .show {
    width: 100%;
    height: auto;
    opacity: 1;
  }
  li {
    a {
      display: block;
      line-height: 22px;
      padding: 14px 16px;
    }
  }
  .selected {
    background-color: rgba(0, 0, 0, 0.03);
  }
  .no-selected {
    color: rgba(0, 0, 0, 0.3);
    background-color: transparent;
  }
}
